import { ApplicationModel } from '@app/application.model';
import { Injectable } from '@angular/core';
import { select } from '@ngrx/store';
import { Observable } from 'rxjs';

import {
  TransferLoadAction,
  TransferResetAction
} from '@store/actions/models/transfer-account/transfer-account-action';


@Injectable()
export class TransferModel extends ApplicationModel {

  public isLoadingTransfer$: Observable<boolean> = this.store.pipe(
    select(store => store.models.account_transfer.loading)
  );

  public isErrorTransfer$: Observable<boolean> = this.store.pipe(
    select(store => store.models.account_transfer.error)
  );

  public isloadedTransfer$: Observable<boolean> = this.store.pipe(
    select(store => store.models.account_transfer.loaded)
  );


  public fetchTransfer(accountFrom: number, accountTo: number, amount: number): void {
    this.store.dispatch(new TransferLoadAction(accountFrom, accountTo, amount));
  }

  public fetchReset(): void {
    this.store.dispatch(new TransferResetAction());
  }
}
