import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { environment as ENV } from '@environment';
import { AccountTransferInterface } from '@core/interfaces/accountTransfer.interface';

@Injectable()
export class TransferService {

  constructor(private http: HttpClient) { }

  public accoutTransfer(accountFrom: number, accountTo: number, amount: number ): Observable<AccountTransferInterface> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const transfer = {
      'accountFromInformation': {
        'accountIdentifier': accountFrom,
        'productType': 'CURRENT_ACCOUNT',
      },
      'accountToInformation': {
        'accountIdentifier': accountTo,
        'productType': 'CURRENT_ACCOUNT',
        'bank': 'BANCO_POPULAR'
      },
      'transferInformation': {
        'amount': amount
      }
    };

    return this.http.post<AccountTransferInterface>(ENV.api.base + ENV.api.services.account_transfer, transfer, httpOptions);

  }
}
