import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Observable, Subscription, timer } from 'rxjs';
import { isNullOrUndefined } from 'util';

import { TransferModel } from '@modules/transfer-to-account/transfer.model';

@Component({
  selector: 'app-transfer-to-account',
  templateUrl: './transfer-to-account.component.html',
  styleUrls: ['./transfer-to-account.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class TransferToAccountComponent implements OnInit, OnDestroy {

  public formTransfer: FormGroup;
  public disabledButton: boolean = false;
  private subscriptions: Array<Subscription> = [];

  constructor(private model: TransferModel) { }

  ngOnInit(): void {
    this.initForm();

    this.isSuccess.subscribe(data => {
      if (data) {
        this.formTransfer.reset();
        timer(2000).subscribe(_ => {
          this.model.fetchReset();
        });
      }
    });

    this.isLoading.subscribe(data => this.disabledButton = data);

  }

  ngOnDestroy(): void {
    this.model.fetchReset();
    this.subscriptions
      .filter((subscription: Subscription) => !isNullOrUndefined(subscription))
      .forEach((subscription: Subscription) => subscription.unsubscribe());
  }

  get isLoading(): Observable<boolean> {
    return this.model.isLoadingTransfer$;
  }

  get isError(): Observable<boolean> {
    return this.model.isErrorTransfer$;
  }

  get isSuccess(): Observable<boolean> {
    return this.model.isloadedTransfer$;
  }

  public fetchTransfer(): void {
    this.model
      .fetchTransfer(this.formTransfer.value.account_origin, this.formTransfer.value.account_destination, this.formTransfer.value.amount);
  }

  private initForm(): void {
    this.formTransfer = new FormGroup({
      'account_origin': new FormControl('', [
        Validators.required,
        Validators.minLength(5),
        Validators.min(1),
        Validators.pattern(/^[0-9]+$/)
      ]),
      'account_destination': new FormControl('',
        [
          Validators.required,
          Validators.minLength(5),
          Validators.min(1),
          Validators.pattern(/^[0-9]+$/)
        ]),
      'amount': new FormControl('', [
        Validators.required,
        Validators.min(1),
        Validators.pattern(/^[0-9]+$/)
      ])
    });
  }

}
