import { catchError, map, switchMap } from 'rxjs/operators';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { Action } from '@ngrx/store';
import { Observable, of } from 'rxjs';

import * as fromAccountTransfer from '@store/actions/models/transfer-account/transfer-account-action';
import { TransferService } from '@modules/transfer-to-account/services/transfer/transfer.service';
import { AccountTransferInterface } from '@core/interfaces/accountTransfer.interface';

@Injectable()
export class TransferEffect {

  constructor(
    private actions$: Actions,
    private transferService: TransferService
  ) { }

  @Effect()
  LoadTransfer: Observable<Action> = this.actions$.pipe(
    ofType<fromAccountTransfer.TransferLoadAction>(fromAccountTransfer.TRANSFER_LOAD),
    switchMap((action: fromAccountTransfer.TransferLoadAction) => {
      return this.transferService.accoutTransfer(action.accountFrom, action.accountTo, action.amount)
        .pipe(
          map((items: AccountTransferInterface) => new fromAccountTransfer.TransferSuccessAction()),
          catchError(_ => of(new fromAccountTransfer.TransferFailAction()))
        );
    })
  );
}
